import React, { useState, useRef, useEffect } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
import googleCalendarPlugin from "@fullcalendar/google-calendar"; // //GOOGLE CALENDAR
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "./App.css";

function App() {
  const today = new Date();
  const [currentDate, setCurrentDate] = useState(today);
  const [images, setImages] = useState([]); // State to hold images for the current month
  const calendarRef = useRef(null); // FullCalendar   ref

  // (GOOGLE CALENDAR)
  const [selectedEvent, setSelectedEvent] = useState(null); // !!!   
  const [showEventModal, setShowEventModal] = useState(false); // !!!   

  const currentMonth = currentDate.getMonth() + 1;
  const currentYear = currentDate.getFullYear();

  //   
  const [previewImages, setPreviewImages] = useState([]); //   
  const [uploadedImages, setUploadedImages] = useState([]); //   

  const handlePrevMonth = () => {
    if (calendarRef.current) {
      const calendarApi = calendarRef.current.getApi();
      calendarApi.prev(); // FullCalendar   
      const currentCenterDate = new Date(
        calendarApi.getDate().getFullYear(),
        calendarApi.getDate().getMonth(),
        15 //  (15) 
      );
      setCurrentDate(currentCenterDate);
      console.log(
        `!!!!!!!!!!!!!!!   : ${currentCenterDate.toISOString().split("T")[0]
        }`
      );
    }
  };

  const handleNextMonth = () => {
    if (calendarRef.current) {
      const calendarApi = calendarRef.current.getApi();
      calendarApi.next(); // FullCalendar   
      const currentCenterDate = new Date(
        calendarApi.getDate().getFullYear(),
        calendarApi.getDate().getMonth(),
        15 //  (15) 
      );
      setCurrentDate(currentCenterDate);
      console.log(
        `!!!!!!!!!!!!!!!   : ${currentCenterDate.toISOString().split("T")[0]
        }`
      );
    }
  };

  const handleToday = () => {
    if (calendarRef.current) {
      const calendarApi = calendarRef.current.getApi();
      calendarApi.today(); // FullCalendar   
      const todayDate = new Date(
        calendarApi.getDate().getFullYear(),
        calendarApi.getDate().getMonth(),
        15 //  (15) 
      );
      setCurrentDate(todayDate); //  
      console.log(
        `!!!!!!!!!!!!!!!   : ${todayDate.toISOString().split("T")[0]
        }`
      );
    }
  };

  // Fetch images for the current year and month
  useEffect(() => {
    const fetchImages = async () => {
      const monthString = currentMonth.toString().padStart(2, "0");
      const folderPath = `${process.env.PUBLIC_URL}/images/${currentYear}/${monthString}`;

      try {
        // Fetch image list dynamically (simulate fetching)
        const response = await fetch(`${folderPath}/images.json`);
        if (response.ok) {
          const imageList = await response.json();
          setImages(imageList.map((img) => `${folderPath}/${img}`));
        } else {
          setImages([]); // Clear images if not available
        }
      } catch (error) {
        console.error("Error fetching images:", error);
        setImages([]);
      }
    };

    fetchImages();
  }, [currentYear, currentMonth]);

  //GOOGLE MODAL
  const handleEventClick = (info) => {
    info.jsEvent.preventDefault(); // !!!   
    setSelectedEvent(info.event); // !!!    
    setShowEventModal(true); // !!!  
    console.log("!!!!!!!!  :", info.event);
  };
  //GOOGLE MODAL
  const closeModal = () => {
    setShowEventModal(false); // !!!  
    setSelectedEvent(null); // !!!   
  };

  //GOOGLE MODAL   
  const convertToKST = (date) => {
    if (!date) return "  ";
    const options = {
      year: "numeric",
      month: "2-digit",
      day: "2-digit",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      timeZone: "Asia/Seoul", // KST 
    };
    return new Date(date).toLocaleString("ko-KR", options);
  };

  // UPLOAD MODAL  
  const handleUploadModalDragEnter = (e) => {
    e.target.setAttribute(
      "style",
      "border: 1px dotted lightgray;color:lightgray;"
    );
  };
  const handleUploadModalDragLeave = (e) => {
    e.target.setAttribute("style", "border: 1px  dotted gray;color:gray;");
  };

  //
  //     
  const handleFileDrop = (e) => {
    e.preventDefault();

    const files = Array.from(e.dataTransfer.files);
    const imageFiles = files.filter((file) => file.type.startsWith("image/"));

    if (imageFiles.length === 0) {
      alert("    .");
      return;
    }

    // !!!!!!!!!!!!!!     
    const previewUrls = imageFiles.map((file) => URL.createObjectURL(file));
    setPreviewImages(previewUrls); //  URL 
    setUploadedImages(imageFiles); //     
  };

  //     
  const handleUpload = async () => {
    if (previewImages.length === 0) {
      alert("  .");
      return;
    }

    //     
    setUploadedImages(previewImages);
    //GITHUB ACTION   REPOSITORY   PRIVATE to PRIVATE REPOSITORY   


    // FormData  
    const formData = new FormData();
    previewImages.forEach((file, index) => {
      formData.append(`file-${index}`, file);
    });

    // GITHUB ACTION
    // GitHub Actions  API 
    try {
      // const response = await fetch("https://api.github.com/repos/<YOUR_USERNAME>/<REPO_NAME>/actions/workflows/target-repo-upload.yml/dispatches", {
      const response = await fetch(
        "https://api.github.com/repos/ALL-MY-PROJECTS-2025/CALENDAR/", //   ,
        {
          method: "PUT",
          headers: {
            Authorization: `Bearer `, // PAT
            Accept: "application/vnd.github.v3+json",
          },
          body: JSON.stringify(
            uploadedImages.map((file) => ({
              message: `Upload ${file.name}`,
              content: file, //  Base64   
              branch: "main", //  
            }))
          ),
        }
      );

      if (response.ok) {
        console.log("GitHub Actions  ");
        alert("  .");
      } else {
        console.error("GitHub Actions  ", response);
        alert("  .");
      }
    } catch (error) {
      console.error("API    :", error);
    }

    //     (  )
    console.log(" :", previewImages);

    //      
    setPreviewImages([]);
  };

  //    
  const handleRemoveImage = (index) => {
    setPreviewImages((prev) => prev.filter((_, i) => i !== index)); //   
  };

  return (
    <div className="App">
      <div className="postcard-container">
        <div className="controller">
          <button
            type="button"
            className="btn btn-primary setting-btn"
            data-bs-toggle="modal"
            data-bs-target="#staticBackdrop"
          >
            <span className="material-symbols-outlined">settings</span>
          </button>

          <button
            type="button"
            className="btn btn-primary upload-btn"
            data-bs-toggle="modal"
            data-bs-target="#staticBackdrop2"
          >
            <span className="material-symbols-outlined">upload</span>
          </button>
        </div>

        <div className="photo-frame">
          {/* SWIPER     */}
          {/* {images.length > 0 ? (
            <Swiper
              spaceBetween={0}
              slidesPerView={1}
              // effect="fade"
              autoplay={{
                delay: 1000,
                disableOnInteraction: false,
              }}
            >
              {images.map((img, index) => (
                <SwiperSlide key={index}>
                  <img
                    src={img}
                    alt={`${currentYear} ${currentMonth}  ${index + 1}`}
                    className="month-image"
                    onError={(e) => {
                      e.target.onerror = null;
                      e.target.src = '/images/default.png';
                      e.target.className = 'month-image default-image';
                    }}
                  />
                </SwiperSlide>
              ))}
            </Swiper>
          ) : (
            <img
              src='/images/default.png'
              alt='Default Image'
              className='month-image default-image'
            />
          )} */}

          {/* SLIDE   */}

          {images.length > 0 ? (
            <div className="items">
              {images.map((img, index) => (
                <div key={index} className="item">
                  <img
                    src={img}
                    alt={`${currentYear} ${currentMonth}  ${index + 1
                      }`}
                    className="month-image"
                  />
                  <video autoPlay muted loop>
                      <source src="https://mcard.fromtoday.co.kr/mcard/assets/flower_00.mp4" />
                  </video>
                </div>
              ))}
            </div>
          ) : (
            <div style={{ objectFit: "cover" }}>
              <img
                style={{ width: "100vw", height: "40vh", objectFit: "contain" }}
                src={`${process.env.PUBLIC_URL}/images/default.png`}
                alt="Default Image"
                className="month-image default-image"
              />
            </div>
          )}
        </div>

        <div className="calendar-container">
          <div className="calendar-header">
            <button onClick={handlePrevMonth} className="nav-button">
              <span className="material-symbols-outlined" style={{fontSize:"2.5rem"}}>arrow_left</span>
            </button>
            <h2 style={{height:"80px", display:"flex",alignItems :"center"}}>
              
              {currentYear} {currentMonth}
            </h2>
            <button onClick={handleNextMonth} className="nav-button">
              <span className="material-symbols-outlined" style={{fontSize:"2.5rem"}}>arrow_right</span>
            </button>
            <button onClick={handleToday} className="today-button">
              
            </button>
          </div>

          {/*  */}
          <FullCalendar
            ref={calendarRef}
            plugins={[dayGridPlugin, interactionPlugin, googleCalendarPlugin]} //  
            initialView="dayGridMonth"
            headerToolbar={false}
            googleCalendarApiKey="AIzaSyA_rJ5q1Jjde3tdinjhSUx9m-ZbpCSkS58" // API  
            events={{
              googleCalendarId:
                "505ad0eb41755b07ffaab2b3b77c58ab9c34e6f6b38d619b3894a5816d162004@group.calendar.google.com", // Google Calendar ID
            }}
            initialDate={currentDate.toISOString().split("T")[0]}
            datesSet={(dateInfo) => {
              const newDate = new Date(dateInfo.startStr);
              const viewCenterDate = new Date(
                newDate.getFullYear(),
                newDate.getMonth() + 1,
                15 //    
              );
              setCurrentDate(viewCenterDate);

              console.log(
                `!!!!!!!!!!!!!!! FullCalendar   ( ): ${viewCenterDate.toISOString().split("T")[0]
                }`
              );
            }}
            dateClick={(info) => {
              console.log("clicked...", info.date);
            }}
            //GOOGLE CALENDAR(// npm install @fullcalendar/google-calendar)
            eventClick={handleEventClick} // !!!    
          />
        </div>

        {/* GOOGLE EVENT MODAL        */}
        {showEventModal && selectedEvent && (
          <div
            className="modal fade show"
            style={{ display: "block", backgroundColor: "rgba(0, 0, 0, 0.5)" }}
            tabIndex="-1"
          >
            <div className="modal-dialog modal-dialog-centered">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title">
                    {selectedEvent.title || "  "}
                  </h5>
                  <button
                    type="button"
                    className="btn-close"
                    onClick={closeModal}
                    aria-label="Close"
                  ></button>
                </div>
                <div className="modal-body">
                  <div className="img-block">
                    <img src={`${process.env.PUBLIC_URL}/modal_1.png`} alt="Example" />
                    <video autoPlay muted loop>
                      <source src="https://mcard.fromtoday.co.kr/mcard/assets/flower_01.mp4" />
                    </video>

                  </div>
                  <div className="modal-schedule">
                    <div>
                      <strong> :</strong>{" "}
                      {convertToKST(selectedEvent.start)}
                    </div>
                    <div>
                      <strong> :</strong>{" "}
                      {convertToKST(selectedEvent.end) || "  "}
                    </div>
                    <div>
                      <strong>:</strong>{" "}
                      {selectedEvent.extendedProps.description || " "}
                    </div>
                    <div>
                      <strong>:</strong>{" "}
                      {selectedEvent.extendedProps.location || "  "}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* END */}

        {/* SETTING Modal */}
        <div
          className="modal fade"
          id="staticBackdrop"
          data-bs-backdrop="static"
          data-bs-keyboard="false"
          tabIndex="-1"
          aria-labelledby="staticBackdropLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog  modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="staticBackdropLabel">
                  
                </h5>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body">
                <div>
                  <div>MENU : </div>
                  <div>VALUE : AA</div>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-bs-dismiss="modal"
                >
                  Close
                </button>
                <button type="button" className="btn btn-primary">
                  Save changes
                </button>
              </div>
            </div>
          </div>
        </div>
        {/* END */}

        {/* UPLOAD MODAL */}
        <div
          className="modal fade  uploadmodal"
          id="staticBackdrop2"
          data-bs-backdrop="static"
          data-bs-keyboard="false"
          tabIndex="-1"
          aria-labelledby="staticBackdropLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog  modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="staticBackdropLabel">
                  UPLOAD{" "}
                </h5>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body">
                <div
                  className="upload-block"
                  onDragEnter={handleUploadModalDragEnter}
                  onDragLeave={handleUploadModalDragLeave}
                  onDragOver={(e) => e.preventDefault()}
                  onDrop={handleFileDrop}
                >
                  +
                </div>
                <div className="preview">
                  {previewImages.length > 0 ? (
                    <div className="preview-container">
                      {previewImages.map((src, index) => (
                        <div
                          key={index}
                          className="preview-image"
                          style={{ position: "relative" }}
                        >
                          <img
                            src={src}
                            alt={`preview-${index}`}
                            style={{
                              width: "100px",
                              height: "100px",
                              objectFit: "cover",
                            }}
                          />
                          {/*   */}
                          <button
                            style={{
                              position: "absolute",
                              top: "0px",
                              right: "20px",
                              backgroundColor: "red",
                              color: "white",
                              border: "none",
                              borderRadius: "25%",
                              width: "20px",
                              height: "20px",
                              display: "flex",
                              alignItems: "center",
                              justifyContent: "center",
                              cursor: "pointer",
                            }}
                            onClick={() => handleRemoveImage(index)}
                          >
                            -
                          </button>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <p>-</p>
                  )}
                </div>
              </div>

              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-bs-dismiss="modal"
                >
                  Close
                </button>
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={handleUpload} //  
                >
                  Upload
                </button>
              </div>
            </div>
          </div>
        </div>
        {/* END */}
      </div>
    </div>
  );
}

export default App;
